<template>
	<div class="about">
		<div class="news" >
			 <el-table
			    :data="tableData"
			    style="width: 100%; margin-top: -40px;">
			    <el-table-column
			      width="1300">
			      <template slot-scope="scope">
			        <i class="el-icon-time"></i>
			        <span style="margin-left: 10px">{{ scope.row.date }}</span>
			      </template>
			    </el-table-column>
			    <el-table-column
			      width="180">
			      <template slot-scope="scope">
			        <el-popover>
			          <div slot="reference" class="name-wrapper">
			            <el-tag size="medium">{{ scope.row.name }}</el-tag>
			          </div>
			        </el-popover>
			      </template>
			    </el-table-column>
			    <el-table-column>
			      <template slot-scope="scope">
					   <el-button 
					    @click="handleDelete(scope.$index, scope.row)"
					   :type="lifttype">{{lifbtn}}</el-button>
			      </template>
			    </el-table-column>
			  </el-table>
			   <el-button :type="butttype" @click="buttBtn" style="margin-top: 30px;">{{buttbtn}}</el-button>
		</div>
	</div>
</template>

<script>
	export default{
		props:{
			tableData:[],
			lifbtn:String,
			buttbtn:String,
			lifttype:String,
			butttype:String,	
		},
			methods:{
				handleDelete(index,row){
					this.$emit('rowClick',row,index)
					// console.log(index)
					// console.log(this.isWan)
				},
				buttBtn(){
					this.$emit('buttBtn')
				}
			},
			mounted(){
				console.log(this.tableData)
			}
	}
</script>

<style scoped>
	.txt{
		text-align: center;
	}
</style>