<template>
	<div id="course">
		<el-tabs type="border-card">
		  <el-tab-pane>
		    <span slot="label"><i class="el-icon-date"></i> 未读消息</span>
		    <CorseNews @rowClick="tabClick" @buttBtn="tabWnt" :tableData="tableData" :lifbtn="'标为已读'" :buttbtn="'全部标为已读'" :butttype="'primary'" :lifttype="'plain'"></CorseNews>
		  </el-tab-pane>
		  <el-tab-pane label="已读消息">
			  
			   <CorseNews @rowClick="newsClick"  @buttBtn="newWnt" :tableData="newsdata" :lifbtn="'删除'" :buttbtn="'全部删除'" :butttype="'danger'" :lifttype="'danger'"></CorseNews>
		  </el-tab-pane>
		  <el-tab-pane label="回收站">
			   <CorseNews @rowClick="recClick" @buttBtn="recWnt" :tableData="recycle" :lifbtn="'还原'" :buttbtn="'清空回收站'" :butttype="'danger'" :lifttype="'plain'"></CorseNews>
		  </el-tab-pane>
		</el-tabs>
	</div>
</template>

<script>
	import CorseNews from '../components/courseNews.vue'
	export default{
		computed:{
			tableData(){
				return this.$store.state.course.tableData
			},
			newsdata(){
				return this.$store.state.course.newsdata
			},
			recycle(){
				return this.$store.state.course.recycle
			}
		},
		components:{
			CorseNews
		},
		methods:{
			tabClick(row,index){
				this.$store.commit('tabClick',row,index)
			},
			newsClick(row,index){
				this.$store.commit('newsClick',row,index)
			},
			recClick(row,index){
				this.$store.commit('recClick',row,index)
			},
			tabWnt(){
				this.$store.commit('tabWnt')
			},
			newWnt(){
				this.$store.commit('newWnt')
			},
			recWnt(){
				this.$store.commit('recWnt')
			}
			
		},
	}
</script>

